import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { FingerCodeComponent } from './finger-code/finger-code.component';
import { StatementsComponent } from './statements/statements.component';
import { AuditService } from '../../../core/_base/services/audit.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AuditModel } from 'src/app/core/_base/models/audit.model';
import { ReferencesService } from 'src/app/core/boarding/_services/references.service';
import { ValidarAspiranteResponseModel } from 'src/app/core/boarding/_models/validarAspiranteResponse.model';
import { DomSanitizer } from '@angular/platform-browser';
import { ConfigService } from '../../../core/_base/services/config.service';

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.scss'],
})
export class AuthComponent implements OnInit {
  formulario: FormGroup;
  msgBienvenida: string;
  numeroContacto: string;
  screenWidth: number;
  divInicioMobile = true;
  auditModel: AuditModel;
  validarAspiranteResponse: ValidarAspiranteResponseModel;
  alertaError = true;
  msgAlertaError: string;
  checkClass = false;
  body = {
    Cliente: 'POSTMAN',
    Token: '123',
    IdAcceso: '10.150.0.142',
    UsuarioFinal: 'PRUEBA LOCAL',
  };

  constructor(
    private auditService: AuditService,
    public dialog: MatDialog,
    private fb: FormBuilder,
    private router: Router,
    private coreService: ReferencesService,
    private sanitizer: DomSanitizer,
    private route: ActivatedRoute,
    private configS: ConfigService
  ) {
    this.formulario = fb.group({
      cedula: [
        { value: '', disabled: false },
        [
          Validators.required,
          Validators.minLength(10),
          Validators.pattern('^[0-9]+$'),
        ],
      ],
      codigoDactilar: [
        { value: '', disabled: false },
        [Validators.required, Validators.minLength(10)],
      ],
      checkAutorizo: [{ value: false, disabled: false }],
    });
  }

  async ngOnInit() {
    document.oncontextmenu = function () {
      return false;
    };
    this.screenWidth = screen.width;
    if (this.screenWidth <= 768) {
      setTimeout(
        function () {
          this.divInicioMobile = false;
        }.bind(this),
        3000
      );
    }
    this.msgBienvenida = this.configS.getValue('MENSAJE_BIENVENIDA');
    this.numeroContacto = this.configS.getValue('NUM_CALL_CENTER');
    this.auditService.star();
    this.auditModel = new AuditModel();
    this.auditModel.clear();
    this.validarAspiranteResponse = new ValidarAspiranteResponseModel();
    this.validarAspiranteResponse.clear();
    this.auditModel = this.auditService.getAudit();
  }

  fingerCode() {
    const dialogRef = this.dialog.open(FingerCodeComponent, {
      maxHeight: '90vh',
      maxWidth: '90vw',
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (!res) {
        return;
      }
    });
  }

  statements() {
    const dialogRef = this.dialog.open(StatementsComponent, {
      maxHeight: '90vh',
      maxWidth: '90vw',
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (!res) {
        return;
      }
    });
  }

  async logIn() {
    if (this.validateCI(this.formulario.get('cedula').value)) {
      if (this.formulario.get('checkAutorizo').value) {
        // Aqui se debe cambiar this.body por this.auditModel
        const body = {
          Auditoria: this.body,
          Cedula: this.formulario.get('cedula').value,
          CodigoDactilar: this.formulario.get('codigoDactilar').value,
          AutorizacionConsulta: this.formulario.get('checkAutorizo').value,
        };
        this.validarAspiranteResponse = await this.coreService
          .ConsultaValidarAspiranteCuentaDigital(body)
          .toPromise();
        sessionStorage.setItem(
          'ValidaAspirante',
          JSON.stringify(this.validarAspiranteResponse)
        );
        this.auditService.setFinalUser(
          this.validarAspiranteResponse.DatosAspirante.Nombres +
            ' ' +
            this.validarAspiranteResponse.DatosAspirante.Apellidos
        );
        this.router.navigateByUrl('/boarding');
      } else {
        this.msgAlertaError = 'Cliente no ha autorizado su consulta';
        this.alertaError = false;
        this.checkClass = true;
      }
    } else {
      this.msgAlertaError = 'Número de Cédula Erróneo';
      this.alertaError = false;
      this.checkClass = false;
    }
  }

  cerrarAlertaError() {
    this.alertaError = true;
  }

  validateKey(evt) {
    let code = evt.which ? evt.which : evt.keyCode;
    if (code == 8) {
      return true;
    } else if (code >= 48 && code <= 57) {
      return true;
    } else {
      return false;
    }
  }

  validateCI(ced) {
    let total = 0;
    const length = 10;
    const longcheck = length - 1;
    if (ced !== '' && length === 10) {
      for (let i = 0; i < longcheck; i++) {
        if (i % 2 === 0) {
          const char = ced.charAt(i);
          let aux = +char * 2;
          if (aux > 9) {
            aux -= 9;
          }
          total += aux;
        } else {
          // tslint:disable-next-line: radix
          total += parseInt(ced.charAt(i));
        }
      }
      total = total % 10 ? 10 - (total % 10) : 0;
      if (+ced.charAt(length - 1) === total) {
        return true;
      }
      return false;
    }
  }
}
