import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { ParametrosResponseModel } from '../_models/parametrosResponse.model';
import { ParametrosInicialesResponse } from '../_models/parametros-inicialesResponse.model';

@Injectable()
export class ParametersService {

    private readonly apiUrl = 'http://192.168.33.12:81';

    private readonly parameters: string = '/api/GetParametros';
    private readonly initialParameters: string = '/api/ConParametrosIniciales';

    constructor(
        private http: HttpClient
    ) { }

    private get urlParameters() {
        return this.apiUrl + this.parameters;
    }

    private get urlInitialParameters() {
        return this.apiUrl + this.initialParameters;
    }

    GetParameters(): Observable<ParametrosResponseModel> {
        const endpointUrl = `${this.urlParameters}`;
        return this.http.get<ParametrosResponseModel>(endpointUrl);
    }

    GetInitialParameters(body: any): Observable<ParametrosInicialesResponse> {
        const endpointUrl = `${this.urlInitialParameters}`;
        return this.http.post<ParametrosInicialesResponse>(endpointUrl, body);
    }
}

