import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {environment} from '../../../../environments/environment';
import { Guid } from 'guid-typescript';
import {AuditModel} from '../models/audit.model';
import {Router} from '@angular/router';

const Cliente = 'WebOnboarding';
const UsuarioFonalIni = 'Desconocido';
const VarName = 'Audit';

@Injectable({
  providedIn: 'root'
})

export class AuditService {
  constructor(private router: Router,
              private http: HttpClient) {}

  star(){
    const tempAudit = new AuditModel();
    tempAudit.clear();
    this.getIPAddress().subscribe( next => {
      tempAudit.Cliente = Cliente;
      tempAudit.IdAcceso = next.ip;
      tempAudit.TipoAcceso = 'IP';
      tempAudit.Token = String(Guid.create());
      tempAudit.UsuarioFinal = UsuarioFonalIni;
      sessionStorage.setItem(VarName, btoa(JSON.stringify(tempAudit)));
      return tempAudit;
      });
  }

  getAudit(){
    if (sessionStorage.getItem(VarName) !== null){
      const guardado = atob(sessionStorage.getItem(VarName));
      return JSON.parse(guardado);
    }else{
      this.router.navigate(['/']);
    }
  }

  setFinalUser(user: string){
    const tempAudit = this.getAudit();
    tempAudit.UsuarioFinal = user;
    sessionStorage.setItem(VarName, btoa(JSON.stringify(tempAudit)));
  }

  getVarName(){
    return VarName;
  }

  getIPAddress() {
    // return  res.ip;
    return this.http.get<any>('https://api.ipify.org/?format=json');
  }

}
