import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { BackReplyModel } from '../_models/back-reply.model';
import { ValidarAspiranteResponseModel } from '../_models/validarAspiranteResponse.model';
import { CodigoActivacionResponse } from '../_models/codigoactivacionResponse.model';

@Injectable()
export class ReferencesService {

    private readonly apiUrl = 'http://192.168.33.12:81';

    private readonly tiposTelefono: string = '/api/ConTiposTelefono';
    private readonly entidadesFinancieras: string = '/api/ConEntidadesFinancieras';
    private readonly tiposProductos: string = '/api/ConTiposProducto';
    private readonly validarAspiranteCuentaDigital: string = '/api/ValidarAspiranteCuentaDigital';
    private readonly codigoActivacion: string = '/api/CodigoActivacion';

    constructor(
        private http: HttpClient
    ) { }

    private get urlTiposTelefono() {
        return this.apiUrl + this.tiposTelefono;
    }
    private get urlEntidadesFinancieras() {
        return this.apiUrl + this.entidadesFinancieras;
    }
    private get urlTiposProductos() {
        return this.apiUrl + this.tiposProductos;
    }
    private get urlValidarAspiranteCuentaDigital() {
        return this.apiUrl + this.validarAspiranteCuentaDigital;
    }
    private get urlCodigoActivacion() {
        return this.apiUrl + this.codigoActivacion;
    }

    ConsultaTiposTelefono(body: any): Observable<BackReplyModel> {
        const endpointUrl = `${this.urlTiposTelefono}`;
        return this.http.post<BackReplyModel>(endpointUrl, body);
    }

    ConsultaEntidadesFinancieras(body: any): Observable<BackReplyModel> {
        const endpointUrl = `${this.urlEntidadesFinancieras}`;
        return this.http.post<BackReplyModel>(endpointUrl, body);
    }

    ConsultaTiposProductos(body: any): Observable<BackReplyModel> {
        const endpointUrl = `${this.urlTiposProductos}`;
        return this.http.post<BackReplyModel>(endpointUrl, body);
    }

    ConsultaValidarAspiranteCuentaDigital(body: any): Observable<ValidarAspiranteResponseModel> {
        const endpointUrl = `${this.urlValidarAspiranteCuentaDigital}`;
        return this.http.post<ValidarAspiranteResponseModel>(endpointUrl, body);
    }

    ConsultaCodigoActivacion(body: any): Observable<CodigoActivacionResponse> {
        const endpointUrl = `${this.urlCodigoActivacion}`;
        return this.http.post<CodigoActivacionResponse>(endpointUrl, body);
    }
}

