export class DomicilioModel {
	
  Provincia: string;
  Canton: string;
  Parroquia: string;
  CodProvincia: string;
  CodCanton: string;
  CodParroquia: string;
  Direccion: string;
  Referencia: string;
  Coordenadas: string;
  CodigoArea: string;
  Telefono: string;
  TipoVivienda: string;
  ValorVivienda: number;
  TiempoResidencia: number;

  clear() {
    this.Provincia = '';
    this.Canton = '';
    this.Parroquia = '';
    this.CodProvincia = '';
    this.CodCanton = '';
    this.CodParroquia = '';
    this.Direccion = '';
    this.Referencia = '';
    this.Coordenadas = '';
    this.CodigoArea = '';
    this.Telefono = '';
    this.TipoVivienda = '';
    this.ValorVivienda = 0;
    this.TiempoResidencia = 0;
  }
}
