import { Component, OnInit } from '@angular/core';
import {MatDialogRef} from '@angular/material/dialog';
import {ParametersService} from '../../../../core/boarding/_services/parameters.service';
import {normalizeExtraEntryPoints} from '@angular-devkit/build-angular/src/angular-cli-files/models/webpack-configs';
import {AuditService} from '../../../../core/_base/services/audit.service';

@Component({
  selector: 'app-statements',
  templateUrl: './statements.component.html',
  styles: [
  ]
})
export class StatementsComponent implements OnInit {
  LeyendaAutorizacion = 'Autorizo de forma expresa a Banco Machala S.A para que obtenga o verifique mis datos';
  constructor(public dialogRef: MatDialogRef<StatementsComponent>, public auditService: AuditService, public parametersService: ParametersService) { }

  ngOnInit(): void {
    this.parametersService.GetInitialParameters(this.auditService.getAudit()).subscribe(
      data => {
        this.LeyendaAutorizacion = data.LeyendaAutorizacion;
      });
  }
  close(): void {
    this.dialogRef.close();
  }

}
