import {FotoModel} from './foto.model';
import {ConyugeModel} from './conyuge.model';
import {DomicilioModel} from './domicilio.model';
import {InformacionEconomicaModel} from './informacion-economica.model';
import {ReferenciaPersonalModel} from './referencia-personal.model';
import {ReferenciaBancariaModel} from './referencia-bancaria.model';
import {ResidenciaFiscalExtranjeroModel} from './residencia-fiscal-extranjero.model';
import {EntregaTarjetaModel} from './entrega-tarjeta.model';


export class DatosAspiranteModel {
  Cedula: string;
  CodigoDactilar: string;
  SelfieVida: boolean;
  Fotos?: FotoModel[];
  Nombres: string;
  Apellidos: string;
  FechaNacimiento: Date;
  CodPaisNacionalidad: string;
  EstadoCivil: string;
  Conyuge: ConyugeModel;
  Celular: string;
  CorreoElectronico: string;
  Domicilio: DomicilioModel;
  EntregaTarjeta: EntregaTarjetaModel;
  InformacionEconomica: InformacionEconomicaModel;
  ReferenciasPersonales: ReferenciaPersonalModel[];
  TieneCuentasSistemaFinancieroNacional: boolean;
  ReferenciasBancarias: ReferenciaBancariaModel[];
  TieneResidFiscalExtranjero: boolean;
  ResidFiscalExtranjero: ResidenciaFiscalExtranjeroModel;

  clear() {
    this.Cedula = '';
    this.CodigoDactilar = '';
    this.SelfieVida = false;
    this.Fotos = [];
    this.Nombres = '';
    this.Apellidos = '';
    this.FechaNacimiento = new Date();
    this.CodPaisNacionalidad = '';
    this.EstadoCivil = '';
    this.Conyuge = new ConyugeModel();
    this.Celular = '';
    this.CorreoElectronico = '';
    this.Domicilio = new DomicilioModel();
    this.InformacionEconomica = new InformacionEconomicaModel();
    this.ReferenciasPersonales = [];
    this.TieneCuentasSistemaFinancieroNacional = false;
    this.ReferenciasBancarias = [];
    this.TieneResidFiscalExtranjero = false;
    this.ResidFiscalExtranjero = new ResidenciaFiscalExtranjeroModel();
  }
}
